import colorPicker from './color-picker';
import backToTop from './back-top';
const components = [
    colorPicker,
    backToTop
];

const install = Vue => {
    if (install.installed) {
        return;
    }

    components.map(component => {
        Vue.component(component.name, component);
    });
};

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    install,
    colorPicker,
    backToTop
};
